/* üåû Page Background */
body {
  background-color: #ffeaa7;
  background-image: radial-gradient(#0091ff 7.2%, transparent 0);
  background-size: 25px 25px;
  min-height: 70vh;
  padding: 1rem;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Architects Daughter", cursive;
  font-size: 130%;
  color: hsl(198, 1%, 29%);
  text-align: center;
}

/* üì¶ Main Container */
.container {
  width: 100%;
  max-width: 500px;
  min-width: 250px;
  min-height: 500px;
  padding: 1rem;
  box-sizing: border-box;
  background: #f1f5f8;
  background-image: radial-gradient(#bfc0c1 7.2%, transparent 0);
  background-size: 25px 25px;
  border-radius: 20px;
  box-shadow: 4px 3px 7px 2px #00000040;
}

/* üìù Heading */
.heading {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
}

.heading h1 {
  background-color: #fdcb6e;
  font-size: 1.5rem;
  padding: 0.2rem 1.2rem;
  transform: rotate(2deg);
  border-radius: 20% 5% 20% 5% / 5% 20% 25% 20%;
}

/* üî§ Input Field */
.form input {
  width: 70%;
  padding: 0.7rem;
  margin-bottom: 20px;
  font-size: 1rem;
  font-family: inherit;
  color: hsla(260, 2%, 25%, 0.7);
  background-color: transparent;
  border: solid 3px transparent;
  border-bottom: dashed 3px #fdcb6e;
  border-bottom-right-radius: 15px 3px;
  border-bottom-left-radius: 3px 15px;
  box-sizing: border-box;
}

/* ‚ûï Add Button */
button {
  padding: 0;
  border: none;
  background-color: #ffeaa7;
  font-family: inherit;
  border-radius: 5px;
  text-decoration: none;
  padding-bottom: 3px;
}

button span {
  display: block;
  padding: 0.5rem 1rem;
  background: #f1f5f8;
  border-radius: 5px;
  border: 2px solid hsl(198, 1%, 29%);
}

/* üìã Task List */
ul {
  padding-left: 0;
  margin-left: 0;
  width: 100%;
}

li {
  text-align: left;
  max-width: 80%;
  word-break: break-word;
  overflow-wrap: break-word;
}

/* ‚úÖ Task Item */
.todo-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0.5rem 0;
  margin-bottom: 0.5rem;
  border-radius: 8px;
}

/* üóíÔ∏è Task Text */
.task-text {
  flex: 1;
  word-break: break-word;
  overflow-wrap: break-word;
}

/* üóëÔ∏è Delete Button */
.delete-btn {
  background: none;
  border: none;
  color: #ff4d4d;
  cursor: pointer;
  font-size: 1.4rem;
  padding: 0.3rem;
  margin-left: auto;
  transition: color 0.2s ease, transform 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.delete-btn:hover {
  color: #ff1a1a;
  transform: scale(1.1);
}

/* üí¨ Tooltip */
.delete-btn[aria-label]::after {
  content: attr(aria-label);
  position: absolute;
  top: -30px;
  right: 0;
  background: #333;
  color: #fff;
  padding: 0.3rem 0.6rem;
  border-radius: 4px;
  font-size: 0.75rem;
  white-space: nowrap;
  opacity: 0;
  transform: translateY(0);
  transition: opacity 0.2s ease, transform 0.2s ease;
  pointer-events: none;
}

.delete-btn:hover[aria-label]::after {
  opacity: 1;
  transform: translateY(-10px);
}
